class DashboardComponent {
    constructor(dashboardService, router) {
        this.dashboardService = dashboardService;
        this.router = router;
        this.isLoader = false;
        this.upcomingCount = 0;
        this.pendingCount = 0;
        this.completedCount = 0;
        this.tickets = [];
    }
    ngOnInit() {
        this.getData();
    }
    getData() {
        this.isLoader = true;
        this.dashboardService.getLabAttendantDashboard().subscribe((res) => {
            if (res.status == '200') {
                this.upcomingCount = res.data.upcoming;
                this.pendingCount = res.data.pending;
                this.completedCount = res.data.completed;
                this.tickets = res.data.labReports;
                this.isLoader = false;
            }
        });
    }
    gotoHospitalProfile(id) {
        this.router.navigate([`/app/ticket`]);
    }
}


